<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">required</code> 指定 wmts 服务地址。</td>
</tr>
<tr>
<td>format</td>
<td>String</td>
<td><code v-pre="">'image/jpeg'</code></td>
<td><code v-pre="">optional</code> 指定服务的 MIME 类型。</td>
</tr>
<tr>
<td>layer</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> 指定 WMTS 请求图层名称。</td>
</tr>
<tr>
<td>wmtsStyle</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> 指定 WMTS 请求样式名称。</td>
</tr>
<tr>
<td>tileMatrixSetID</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> 指定 WMTS 请求的 TileMatrixSet 的标识符。</td>
</tr>
<tr>
<td>tileMatrixLabels</td>
<td>Array</td>
<td></td>
<td><code v-pre="">optional</code> 指定 TileMatrix 中用于 WMTS 请求的标识符列表，每个 TileMatrix 级别一个。</td>
</tr>
<tr>
<td>clock</td>
<td>Clock</td>
<td></td>
<td><code v-pre="">optional</code> 确定时间维度值时使用的 Clock 实例。 指定 options.times 时必需。</td>
</tr>
<tr>
<td>times</td>
<td>TimeIntervalCollection</td>
<td></td>
<td><code v-pre="">optional</code> TimeIntervalCollection，其 data 属性是一个包含时间动态维度及其值的对象。</td>
</tr>
<tr>
<td>dimensions</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定包含静态尺寸及其值的对象。</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> 像元宽度。</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> 像元高度。</td>
</tr>
<tr>
<td>tilingScheme</td>
<td>TilingScheme</td>
<td></td>
<td><code v-pre="">optional</code> 指定切片方案。</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 图层的矩形范围,此矩形限制了影像可见范围。 <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 图层可以显示的最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 图层可以显示的最大层级，undefined 表示没有限制。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Ellipsoid</td>
<td></td>
<td><code v-pre="">optional</code> 参考椭球体，没指定默认 WGS84 椭球。</td>
</tr>
<tr>
<td>credit</td>
<td>Credit| String</td>
<td></td>
<td><code v-pre="">optional</code> 数据源描述信息。</td>
</tr>
<tr>
<td>subdomains</td>
<td>String | Array</td>
<td><code v-pre="">'abc'</code></td>
<td><code v-pre="">optional</code> 指定 URL 模板中{s}占位符的子域。 如果此参数是单个字符串，则字符串中的每个字符都是子域。 如果是数组，则数组中的每个元素都是子域。</td>
</tr>
<tr>
<td>token</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定服务 token。 目前只针对天地图服务。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/WebMapServiceImageryProvider.html">WebMapTileServiceImageryProvider</a></strong></li>
</ul>
